import './component';
import './preview';

Shopware.Service('cmsService').registerCmsBlock({
    name: 'twt-button-teaser',
    label: 'twt.cms.blocks.buttonTeaser.label',
    category: 'themeware-cms',
    component: 'sw-cms-block-twt-button-teaser',
    previewComponent: 'sw-cms-preview-twt-button-teaser',
    defaultConfig: {
        marginBottom: '20px',
        marginTop: '20px',
        marginLeft: '20px',
        marginRight: '20px',
        sizingMode: 'boxed'
    },
    slots: {
        image: {
            type: 'twt-button-teaser',
            default: {
                config: {
                    displayMode: { source: 'static', value: 'standard' }
                },
                data: {
                    media: {
                        value: '/tcinnthemewaregenesis/static/img/cms/preview_two_large.jpg',
                        source: 'default',
                    }
                }
            }
        }

    }
});