import './component';
import './preview';

Shopware.Service('cmsService').registerCmsBlock({
    name: 'twt-grid-one',
    label: 'twt.cms.blocks.gridOne.label',
    category: 'themeware-grids',
    component: 'sw-cms-block-twt-grid-one',
    previewComponent: 'sw-cms-preview-twt-grid-one',
    defaultConfig: {
        marginBottom: '20px',
        marginTop: '20px',
        marginLeft: '20px',
        marginRight: '20px',
        sizingMode: 'boxed',
    },
    slots: {
        'left-top': {
            type: 'image',
            default: {
                config: {
                    displayMode: { source: 'static', value: 'cover' },
                },
                data: {
                    media: {
                        value: '/administration/static/img/cms/preview_camera_large.jpg',
                        source: 'default',
                    },
                },
            },
        },
        'left-bottom': {
            type: 'image',
            default: {
                config: {
                    displayMode: { source: 'static', value: 'cover' },
                },
                data: {
                    media: {
                        value: '/administration/static/img/cms/preview_glasses_large.jpg',
                        source: 'default',
                    },
                },
            },
        },
        'right': {
            type: 'image',
            default: {
                config: {
                    displayMode: { source: 'static', value: 'cover' },
                },
                data: {
                    media: {
                        value: '/administration/static/img/cms/preview_plant_large.jpg',
                        source: 'default',
                    },
                },
            },
        },
    },
});
