import template from './sw-cms-el-twt-call-to-action.html.twig';
import './sw-cms-el-twt-call-to-action.scss';

const { Component, Mixin, Filter, Utils } = Shopware;

Component.register('sw-cms-el-twt-call-to-action', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    computed: {
        displayModeClass() {
            if (this.element.config.displayMode.value === 'standard') {
                return null;
            }

            return `is--${this.element.config.displayMode.value}`;
        },

        styles() {
            return {
                'min-height': this.element.config.displayMode.value === 'cover' &&
                              this.element.config.minHeight.value &&
                              this.element.config.minHeight.value !== 0 ? this.element.config.minHeight.value : '340px',
                'align-self': this.element.config.verticalAlign.value || null
            };
        },

        overlayStyles() {
            return {
                'background-color': this.element.config.colorOverlayBgColor.value || 'rgba(0, 0, 0, 0.85)'
            };
        },

        contentStyles() {
            const backgroundColor = this.element.config.overlay.value;

            if (backgroundColor === true) {
                return {
                    'background-color': this.element.config.bgOverlay.value || 'rgba(255, 255, 255, 0.85)',
                    'color': this.element.config.textColor.value || '#111111'
                };
            }

            if (backgroundColor === false) {
                return {
                    'color': this.element.config.textColor.value || '#111111'
                };
            }
        },

        headlineStyles() {
            return {
                'color': this.element.config.textColorTitle.value || '#111111'
            };
        },

        mediaUrl() {
            const staticFallBackImage = this.assetFilter(`tcinnthemewaregenesis/static/img/cms/preview_three_large.jpg`);
            const elemData = this.element.data.media;
            //const mediaSource = this.element.config.media.source;
            const elemConfig = this.element.config.media;

            //if (mediaSource === 'mapped') {
            if (elemConfig.source === 'mapped') {
                //const demoMedia = this.getDemoValue(this.element.config.media.value);
                const demoMedia = this.getDemoValue(elemConfig.value);

                if (demoMedia && demoMedia.url) {
                    return demoMedia.url;
                }

                //return this.assetFilter('administration/static/img/cms/preview_mountain_large.jpg');
                return staticFallBackImage;
            }

            if (elemConfig.source === 'default') {
                //return this.assetFilter(`/tcinnthemewaregenesis/static/img/cms/preview_three_large.jpg`);
                return this.assetFilter(elemConfig.value);
            }

            if (elemData && elemData.id) {
                return this.element.data.media.url;
            }

            if (elemData && elemData.url) {
                //return this.assetFilter(elemData.url);
                return this.assetFilter(elemConfig.url);
            }

            //return this.assetFilter('administration/static/img/cms/preview_mountain_large.jpg');
            return staticFallBackImage;
        },

        assetFilter() {
            return Filter.getByName('asset');
        },

        mediaConfigValue() {
            //return Utils.get(this.element, 'config.sliderItems.value');
            return this.element?.config?.sliderItems?.value;
        }
    },

    watch: {
        cmsPageState: {
            deep: true,
            handler() {
                this.$forceUpdate();
            }
        },

        mediaConfigValue(value) {
            // const mediaId = Utils.get(this.element, 'data.media.id');
            // const isSourceStatic = Utils.get(this.element, 'config.media.source') === 'static';
            const mediaId = this.element?.data?.media?.id;
            const isSourceStatic = this.element?.config?.media?.source === 'static';

            if (isSourceStatic && mediaId && value !== mediaId) {
                this.element.config.media.value = mediaId;
            }
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('twt-call-to-action'); // 'image'
            this.initElementData('twt-call-to-action'); // 'image'
        }
    }
});
