import template from './sw-cms-el-twt-sidebar-subcategory-navigation.html.twig';
import './sw-cms-el-twt-sidebar-subcategory-navigation.scss';

const { Component } = Shopware;

Component.register('sw-cms-el-twt-sidebar-subcategory-navigation', {
    template,

    mixins: [
        Shopware.Mixin.getByName('cms-element'),
        Shopware.Mixin.getByName('placeholder'),
    ],

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('twt-sidebar-subcategory-navigation');
        },
    },
});
