import template from './sw-theme-manager-detail.html.twig';
import './sw-theme-manager-detail.scss';

const { Component } = Shopware;

Component.override('sw-theme-manager-detail', {
    template,

    data() {
        return {
            selectedBlock: '',
            activeBlocks: {},
        }
    },

    computed: {
        // TWT: Checks if it is a ThemeWare theme
        isThemeWare () {
            if (this.theme && !!this.theme.name) {
                return this.theme.name.toLowerCase().includes('themeware') && !this.theme.name.toLowerCase().includes('cloud');
            }

            return false;
        },

        // TWT: Returns true if accordion is activated in helper plugin config and if it's a ThemeWare theme
        showAccordion () {
            //return this.isThemeWare && this.showAccordionConfig;
            return this.isThemeWare;
        },

        themeWareDetailClasses: function () {
            const classes = [];

            if (this.isThemeWare) {
                classes.push('twt-theme-configuration');
            }

            if (this.showAccordion) {
                classes.push('has-accordion');
            }

            return classes;
        }
    },

    watch: {
        isThemeWare() {
            this.setPageClasses();
        },
    },

    mounted() {
        this.setPageClasses();
    },

    methods: {
        createdComponent() {
            this.$super('createdComponent');
        },

        // TWT: Assign name of clicked block to "activeBlock"
        onBlockClick(blockname, tabName) {
            if(this.showAccordion === false) {
                // TWT: Do nothing if accordion is deactivated
            } else {
                this.activeBlocks[tabName] = blockname;

                // TWT: Assign selected block (used as key to rerender accordion element)
                this.selectedBlock = blockname;
            }
        },

        // TWT: Checks if block is active (used to show/hide content in template)
        isBlockActive(blockName, tabName) {
            // TWT: Blocks are always active if accordion is deactivated
            if(this.showAccordion === false) {
                return true;
            }

            // TWT: Assign first block as active if not already selected for current tab
            if(!this.activeBlocks.hasOwnProperty(tabName)) {
                this.activeBlocks[tabName] = this.firstBlockInTab(tabName);
            }

            return (blockName === this.activeBlocks[tabName]);
        },

        firstBlockInTab(tabName) {
            try {
                let currentTab = this.structuredThemeFields.tabs[tabName];
                return Object.keys(currentTab.blocks)[0];
            } catch (e) {
                console.log(e.toString());
                return null;
            }
        },

        // TWT: Event handler for fullmode-button
        twtStartFullmode(event) {
            document.body.classList.add("twt-fullmode");
        },
        twtExitFullmode(event) {
            document.body.classList.remove("twt-fullmode");
        },
		 
        // TWT: Event handler for Accordion Expand/Collapse All
        twtAccordionExpandAll(event) {
            document.body.classList.add("twt-accordion-expand");
        },
        twtAccordionCollapseAll(event) {
            document.body.classList.remove("twt-accordion-expand");
        },

        setPageClasses() {
            if (this.$el) {
                this.themeWareDetailClasses.forEach((cssClass) => {
                    if (this.isThemeWare) {
                        this.$el.classList.add(cssClass);
                        return;
                    }
                    this.$el.classList.remove(cssClass)
                });
            }
        },
    },
});